import React, { useState, useEffect } from "react";
import Sidebar from "../../components/Admin/Sidebar";
import TopBar from "../../components/Admin//TopBar";
import DataTable from "react-data-table-component";
import { Eye, Lock, Share2, FileText, File, Unlock } from "lucide-react";
import { FaDownload } from "react-icons/fa"; // FontAwesome icons
import axios from "axios";
import { useNavigate, useParams } from "react-router-dom";
function AdminUserAllDetails() {
  const [totalusers, settotalusers] = useState([]);
  const [totalCompany, settotalCompany] = useState([]);
  const [totalModule, settotalModule] = useState([]);
  const { id } = useParams();
  document.title = "Company Detail - Admin";
  const apiUrlAll = "https://blueprintcatalyst.com/api/admin/adminall/";
  const [istotalDocs, settotalDocs] = useState("");
  const [userData, setuserData] = useState("");
  const navigate = useNavigate();
  const [isinvestorReports, setinvestorReports] = useState("");
  const [LockedReports, setLockedReports] = useState("");
  const [UnlockedReports, setUnlockedReports] = useState("");
  const [issharedReport, setsharedReport] = useState("");
  useEffect(() => {
    checkUsersData();
    totalDocs();
    investorReports();
    sharedReports();
  }, []);
  const checkUsersData = async () => {
    let formData = {
      user_id: id,
    };
    try {
      const res = await axios.post(apiUrlAll + "checkUsersData", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;

      if (respo.length === 0) {
        navigate("/admin/company");
      } else {
        setuserData(respo[0]);
      }
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const totalDocs = async () => {
    let formData = {
      user_id: id,
    };
    try {
      const res = await axios.post(apiUrlAll + "totalDocs", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      settotalDocs(respo.length);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const investorReports = async () => {
    let formData = {
      user_id: id,
    };
    try {
      const res = await axios.post(apiUrlAll + "investorReports", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      setinvestorReports(respo.length);
      if (respo.length > 0) {
        const lockedCount = respo.filter((item) => item.is_locked === 1).length;
        const unlockedCount = respo.filter(
          (item) => item.is_locked === 0
        ).length;
        // Set state or use the counts as needed
        setLockedReports(lockedCount);
        setUnlockedReports(unlockedCount);
      }
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const sharedReports = async () => {
    let formData = {
      user_id: id,
    };
    try {
      const res = await axios.post(apiUrlAll + "sharedReports", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      setsharedReport(respo.length);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  //Investor Doc Latest Version
  const [records, setrecords] = useState([]);
  const [recordsPrev, setrecordsPrev] = useState([]);
  const [searchText, setSearchText] = useState("");
  function formatCurrentDate(input) {
    const date = new Date(input); // ✅ Convert input to Date

    if (isNaN(date)) return ""; // ⛔ Invalid date check

    const months = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ];

    const day = date.getDate();
    const month = months[date.getMonth()];
    const year = date.getFullYear();

    const getOrdinal = (n) => {
      if (n >= 11 && n <= 13) return "th";
      switch (n % 10) {
        case 1:
          return "st";
        case 2:
          return "nd";
        case 3:
          return "rd";
        default:
          return "th";
      }
    };

    return `${month} ${day}${getOrdinal(day)}, ${year}`;
  }
  const columns = [
    {
      name: "Report",
      selector: (row) => {
        if (row.type === "investor_update") {
          return "Investor Updates / Reporting";
        } else if (row.type === "dataroom") {
          return "Dataroom";
        } else {
          return row.report;
        }
      },
      sortable: true,
    },
    {
      name: "Date of Report",
      selector: (row) => formatCurrentDate(row.created_at),
      sortable: true,
    },
    {
      name: "Name of report",
      selector: (row) => row.document_name,
      sortable: true,
    },
    {
      name: "Download",
      cell: (row) => (
        <div className="d-flex gap-2">
          <button
            type="button"
            onClick={() => handleDownload(row.downloadUrl)}
            rel="noopener noreferrer"
            className="btn btn-sm btn-outline-success"
            title="Download"
          >
            <FaDownload />
          </button>
        </div>
      ),
      ignoreRowClick: true,
      allowOverflow: true,
      button: true,
    },
  ];
  const handleDownload = async (url) => {
    window.open(url, "_blank");
  };
  const filteredData = records.filter((item) => {
    const search = searchText.toLowerCase();
    return (
      (item.document_name || "").toLowerCase().includes(search) ||
      formatCurrentDate(item.created_at).toLowerCase().includes(search) ||
      (item.download || "").toLowerCase().includes(search)
    );
  });
  useEffect(() => {
    investordocLatestVersion();
    investordocprevVersion();
    handleviewinvestordetailInthis();
  }, []);
  const investordocLatestVersion = async () => {
    let formData = {
      user_id: id,
    };
    try {
      const res = await axios.post(
        apiUrlAll + "investordocLatestVersion",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      var respo = res.data.results;
      console.log(respo);
      setrecords(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const investordocprevVersion = async () => {
    let formData = {
      user_id: id,
    };
    try {
      const res = await axios.post(
        apiUrlAll + "investordocprevVersion",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      var respo = res.data.results;

      setrecordsPrev(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const columnsprev = [
    {
      name: "Report",
      selector: (row) => {
        if (row.type === "investor_update") {
          return "Investor Updates / Reporting";
        } else if (row.type === "dataroom") {
          return "Dataroom";
        } else {
          return row.report;
        }
      },
      sortable: true,
    },
    {
      name: "Date of Report",
      selector: (row) => formatCurrentDate(row.created_at),
      sortable: true,
    },
    {
      name: "Name of report",
      selector: (row) => row.document_name,
      sortable: true,
    },
    {
      name: "Download",
      cell: (row) => (
        <div className="d-flex gap-2">
          <button
            type="button"
            onClick={() => handleDownload(row.downloadUrl)}
            rel="noopener noreferrer"
            className="btn btn-sm btn-outline-success"
            title="Download"
          >
            <FaDownload />
          </button>
          <button
            type="button"
            onClick={() => handleviewinvestordetail(row.id)}
            rel="noopener noreferrer"
            className="btn btn-sm btn-outline-primary"
            title="View Investor"
          >
            <Eye />
          </button>
        </div>
      ),
      ignoreRowClick: true,
      allowOverflow: true,
      button: true,
    },
  ];
  const [recordsinvestor, setrecordsinvestor] = useState([]);
  const [recordsexport, setrecordsexport] = useState([]);
  const [viewInvestorDetail, setviewInvestorDetail] = useState(false);
  const [viewinvestordetailInthis, setviewinvestordetailInthis] = useState("");
  const handleviewinvestordetail = async (investor_updates_id) => {
    let formData = {
      id: investor_updates_id,
      user_id: id,
    };
    try {
      const res = await axios.post(apiUrlAll + "viewinvestorDetail", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      console.log(respo);
      setviewInvestorDetail(true);
      setrecordsinvestor(respo);
      setrecordsexport(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const handleviewinvestordetailInthis = async () => {
    let formData = {
      user_id: id,
    };
    try {
      const res = await axios.post(
        apiUrlAll + "viewinvestordetailInthis",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      var respo = res.data.results;
      setviewinvestordetailInthis(respo.length);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const columnsinvestor = [
    {
      name: "Name of Report",
      selector: (row) => row.document_name,
      sortable: true,
    },
    {
      name: "Date of Report",
      selector: (row) => formatCurrentDate(row.date_view),
      sortable: true,
    },
    {
      name: "First Name",
      selector: (row) => row.first_name,
      sortable: true,
    },
    {
      name: "Last Name",
      selector: (row) => row.last_name,
      sortable: true,
    },
    {
      name: "Email",
      selector: (row) => row.investor_email,
      sortable: true,
    },
    {
      name: "Mobile Number",
      selector: (row) => row.phone,
      sortable: true,
    },
    {
      name: "Ip Address",
      selector: (row) => row.ip_address,
      sortable: true,
    },

    {
      name: "Date Viewed",
      selector: (row) => formatCurrentDate(row.date_view),
      sortable: true,
    },
  ];
  const filteredDatainvestor = recordsinvestor.filter((item) => {
    const search = searchText.toLowerCase();
    return (
      (item.document_name || "").toLowerCase().includes(search) ||
      formatCurrentDate(item.created_at).toLowerCase().includes(search) ||
      (item.download || "").toLowerCase().includes(search)
    );
  });
  const filteredDataprev = recordsPrev.filter((item) => {
    const search = searchText.toLowerCase();
    return (
      (item.document_name || "").toLowerCase().includes(search) ||
      formatCurrentDate(item.created_at).toLowerCase().includes(search) ||
      (item.download || "").toLowerCase().includes(search)
    );
  });
  //Export File
  const [checkval, setcheckval] = useState(false);
  var apiURLINFile = "https://blueprintcatalyst.com/api/user/investorreport/";
  const handleCheckboxChange = (id) => {
    const updatedData = recordsinvestor.map((row) => {
      if (row.id === id) {
        return { ...row, shared: !row.shared };
      }
      return row;
    });

    setrecordsinvestor(updatedData);
    const anySelected = updatedData.some((row) => row.shared === true);
    setcheckval(anySelected);
  };
  const handleExcelFileDownload = async () => {
    const payload = {
      user_id: id,
      update_id: recordsinvestor.map((r) => r.investor_updates_id), // the investor_updates_id you want to export
    };
    const response = await axios.post(
      apiURLINFile + "exportInvestorExcel",
      payload,
      {
        responseType: "blob", // important for file download
      }
    );

    const url = window.URL.createObjectURL(new Blob([response.data]));
    const link = document.createElement("a");
    link.href = url;
    link.setAttribute("download", "investor_report.xlsx");
    document.body.appendChild(link);
    link.click();
  };
  return (
    <>
      <div>
        <div className="d-flex align-items-start gap-0">
          <Sidebar />
          <div className="d-flex flex-column gap-0 w-100">
            <TopBar />
            <section className="dashboard_adminh">
              <div className="container-xl">
                <div className="row gy-4">
                  <div className="col-12">
                    <h2>{userData.company_name}</h2>
                  </div>
                  <div className="col-12">
                    <div className="row gy-4">
                      <div className="col-md-4">
                        <div className="d-flex align-items-center gap-2 dashmain_box">
                          <div className="icon_img">
                            <File />
                          </div>
                          <div className="d-flex flex-column gap-0">
                            <h4>Total Docs</h4>
                            <p>{istotalDocs}</p>
                          </div>
                        </div>
                      </div>
                      <div className="col-md-4">
                        <div className="d-flex align-items-center gap-2 dashmain_box">
                          <div className="icon_img">
                            <FileText />
                          </div>
                          <div className="d-flex flex-column gap-0">
                            <h4>Investor Reports</h4>
                            <p>{isinvestorReports}</p>
                          </div>
                        </div>
                      </div>
                      <div className="col-md-4">
                        <div className="d-flex align-items-center gap-2 dashmain_box">
                          <div className="icon_img">
                            <Unlock />
                          </div>
                          <div className="d-flex flex-column gap-0">
                            <h4>Investor Reports Un Locked</h4>
                            <p>{UnlockedReports}</p>
                          </div>
                        </div>
                      </div>
                      <div className="col-md-4">
                        <div className="d-flex align-items-center gap-2 dashmain_box">
                          <div className="icon_img">
                            <Lock />
                          </div>
                          <div className="d-flex flex-column gap-0">
                            <h4>Investor Reports Locked</h4>
                            <p>{LockedReports}</p>
                          </div>
                        </div>
                      </div>

                      <div className="col-md-4">
                        <div className="d-flex align-items-center gap-2 dashmain_box">
                          <div className="icon_img">
                            <Share2 />
                          </div>
                          <div className="d-flex flex-column gap-0">
                            <h4>Shared Reports</h4>
                            <p>{issharedReport}</p>
                          </div>
                        </div>
                      </div>
                      <div className="col-md-4">
                        <div className="d-flex align-items-center gap-2 dashmain_box">
                          <div className="icon_img">
                            <Eye />
                          </div>
                          <div className="d-flex flex-column gap-0">
                            <h4>Investor Viewed Report</h4>
                            <p>{viewinvestordetailInthis}</p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="col-12">
                    <div className="card p-3">
                      <h5 className="mb-3">Investor Document Latest Version</h5>
                      <div className="d-block">
                        <DataTable
                          columns={columns}
                          data={filteredData}
                          pagination
                          highlightOnHover
                          striped
                          responsive
                        />
                      </div>
                    </div>
                    <div className="card mt-4">
                      <h5 className="mb-3">
                        Investor Document Previous Version
                      </h5>
                      <div className="d-block">
                        <DataTable
                          columns={columnsprev}
                          data={filteredDataprev}
                          pagination
                          highlightOnHover
                          striped
                          responsive
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </div>
        </div>
      </div>
      {viewInvestorDetail && (
        <div
          className="modal fade show d-block"
          style={{ backgroundColor: "rgba(0, 0, 0, 0.5)" }}
          tabIndex="-1"
          role="dialog"
          aria-labelledby="paymentModalLabel"
          aria-hidden="false"
        >
          <div
            className="modal-dialog modal-dialog-centered modal-lg"
            style={{ maxWidth: "1366px" }}
          >
            <div className="modal-content rounded-4 shadow-lg p-4">
              <button
                type="button"
                className="btn-close position-absolute top-0 end-0 m-3"
                onClick={() => setviewInvestorDetail(false)}
                aria-label="Close"
              ></button>
              <h5>Investor Viewed</h5>

              <div className="mt-4">
                {/* Download Button aligned to right */}
                <div className="d-flex justify-content-end mb-2">
                  {/* <button
                    disabled={!checkval}
                    type="button"
                    onClick={() => handleExcelFileDownload()}
                    rel="noopener noreferrer"
                    className="btn btn-sm btn-outline-success"
                    title="Download"
                  >
                    <FaDownload />
                  </button> */}
                </div>

                {/* DataTable */}
                <DataTable
                  columns={columnsinvestor}
                  data={filteredDatainvestor}
                  pagination
                  highlightOnHover
                  striped
                  responsive
                  noDataComponent={
                    <div className="text-center">
                      <span>No results found</span>
                    </div>
                  }
                />
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
}

export default AdminUserAllDetails;
